<template>
  <!-- //TODO: make a UserSource Component with props passing sys -->
  <!-- //TODO: check connection with server using axios  -->
 <h2 hidden >{{state.circle = $route.params.id }}</h2>
<div class="row container" >
  <span >{{state.roleSet}}</span>
  <div class="card border-success mb-3" style="max-width: 18rem;" v-for="(list, i) in state.lists" :key="(list, i)">
  <div class="card-header" ref="nickname1"  >{{list.nickname}} 
    <button type="button" class="btn-close" aria-label="Close" data-bs-toggle="modal" data-bs-target="#exampleModal" @click="sendInfo(list.nickname,list.uid)" 
    v-if="state.roleSet.includes('ROLE_FOUNDER')"></button>
  </div>
  <div class="card-body text-success">
    <h5 class="card-title" v-if="list.emailPublic==true">{{list.email}}</h5>
    <h5 class="card-title" v-else>이메일: 비공개</h5>
    <p class="card-text content" v-if="list.studytopicPublic==true">{{list.aboutme}}</p>
    <p class="card-text " v-else>나의 관심: 비공개</p>
  </div>
  </div>
</div>
<!-- Modal -->
<div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">강제 탈퇴</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
        Do you really want to kick {{modal.name}} out ?
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        <button type="button" class="btn btn-primary" data-bs-dismiss="modal" @click="kickOut(modal,uid)">네, 탈퇴시킵니다</button>
      </div>
    </div>
  </div>
</div>

</template>
<script>
import { onMounted } from 'vue'
import {useRouter} from 'vue-router';
import { reactive,ref,toRef } from '@vue/reactivity'
import axios from 'axios';
export default {
  props: [ 'circleid', 'roleSet' ],
  setup(props) {
    onMounted(() => { getData(); });
    const router = useRouter();
    const circleid = toRef(props,'circleid')
    const roleSet = toRef(props, 'roleSet')
    let nickname1 = ref(null)
    const state = reactive({
      lists   : '',
      circle  :'',
      roleSet :roleSet,
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
    });
    const modal = reactive({
      name  :'',
      uid   :''
    })
    const sendInfo =(name,uid)=>{
      modal.name = name
      modal.uid = uid
    }
    const getData = async () => {
      const url= "http://43.201.60.125/api/circleusers"
      const headers = {
        //TODO send the right amound of tokent and receive the same...
        "Content-Type" : "application/json",
        "Authorization": state.token,
        "token": state.token }
      const body = { circle: state.circle }
      await axios.post(url, body, { headers }).then(function (res) {
        if(res.data != null){
          state.lists = res.data
        } else {
          alert('bad.')
        }
      })
    }
    const kickOut = async () =>{
      console.log("KickOut launched")
      const url= "http://43.201.60.125/api/kickOut"
      const headers = {
        "Content-Type" : "application/json",
        "Authorization": state.token,
        "token": state.token }
      const body = { circleid: state.circle, uid: modal.uid }
      console.log(body.circleid)
      await axios.post(url, body, {headers}).then(function (res){
        if(res.data != null){
          console.log("res.data:: "+res.data)
          alert("Dropped out "+modal.name)
          // router.push({name:"privateProfile"});
          setTimeout(getData(),3000)
        }
      })
      

    }
      return{state,getData, modal, sendInfo,kickOut, nickname1}
  }
}
</script>
<style scoped>
.card {
  border: solid black 1px;
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px;
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
}
.card:hover {
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
  transform: scale(1.01);
}
.content{
  width: 400px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-all;
}
</style>